import { observer } from 'mobx-react-lite'
import React from 'react'

import { LanguageEnum } from '../../../graph/generated'
import { useMenuModel } from '../hooks/useMenu'
import { isCreateId, useMenuItemId } from '../hooks/useMenuItemId'
import { useMenuTranslations } from '../hooks/useMenuTranslations'

type TProps = NoChildren & {
  loadTranslations(language: LanguageEnum): Promise<object | undefined>
}

export const MenuTranslationLoader = observer<TProps>(
  ({ loadTranslations }) => {
    const menuModel = useMenuModel()
    const isCreate = isCreateId(useMenuItemId())
    const { hasTranslation, getTranslationOrCreate } = useMenuTranslations()

    React.useEffect(() => {
      if (
        menuModel.branch &&
        !menuModel.branch.supportedLanguages.includes(menuModel.currentLanguage)
      ) {
        menuModel.setLanguage(menuModel.defaultLanguage)
      }
    }, [menuModel])

    if (menuModel.isDefaultLanguage || isCreate) {
      return null
    }

    // do not override existing translation
    if (hasTranslation(menuModel.currentLanguage)) {
      return null
    }

    loadTranslations(menuModel.currentLanguage).then((translated = {}) => {
      Object.assign(
        getTranslationOrCreate(menuModel.currentLanguage, () => translated),
        translated,
      )
    })

    return null
  },
)
