import { useXFieldState } from '@speedlo/xform'

import { LanguageEnum } from '../../../graph/generated'
import { RM } from '../../../tools/ramda'

const byLanguage = RM.propEq('language')

export interface WithLanguage {
  language: LanguageEnum
}

export interface WithTranslations<T extends WithLanguage> {
  translations: T[]
}

export function useMenuTranslations() {
  const field = useXFieldState<WithLanguage[]>('translations')
  const translations = field.value

  const findTranslation = <T extends WithLanguage>(language: LanguageEnum) => {
    return (
      translations && (translations.find(byLanguage(language)) as T | undefined)
    )
  }

  const getTranslation = <T extends WithLanguage>(
    language: LanguageEnum,
  ): T | undefined => {
    return findTranslation(language)
  }

  const getTranslationOrCreate = <T extends WithLanguage>(
    language: LanguageEnum,
    create: () => Omit<T, 'language'>,
  ): T => {
    const translation = findTranslation<T>(language)
    if (!translation) {
      translations.push({ language, ...create() })
      return getTranslation<T>(language)!
    }
    return translation
  }

  const hasTranslation = (language: LanguageEnum) => {
    return !!findTranslation(language)
  }

  return { hasTranslation, getTranslation, getTranslationOrCreate }
}
